Body{
	margin:0 auto;
}
.App{
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.App-logo {
  -webkit-animation: App-logo-spin  1 reverse 3s linear;
          animation: App-logo-spin  1 reverse 3s linear;
  height: 80px;
}

.App-header {
border:  4px dotted black;
display: -ms-flexbox;
display: flex;
width: 95%;
-ms-flex-pack: distribute;
    justify-content: space-around;
-ms-flex-align: center;
    align-items: center;
}

.Nav{
	border: 1px solid blue;
	width: 35%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-direction: column;
	    flex-direction: column;
	-ms-flex-align: center;
	    align-items: center;
}

.Nav span{
	padding: 5px;
	background-color: orange;
	border: solid black 1px;
	width: 90%;
	text-align: center;
}

.Nav span:hover{
	cursor: pointer;
	background-color: yellow;
}

.Logo{
	border: 1px solid blue;
	width: 25%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-align: center;
	    align-items: center;
	-ms-flex-direction: column;
	    flex-direction: column;
}

.Clock{
	border: 1px solid blue;
	width: 25%;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-pack: center;
	    justify-content: center;
}

.Websites{
	background-color: green;
}


@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
}


@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(180deg); transform: rotate(180deg); }
}



